import $ from 'jquery';

export const parseCats = (data) => {
  return $(data).find('url').toArray().map( (img, i) =>
    ({ id: i, url: img.innerHTML })
  );
};

export let url = 'http://thecatapi.com/api/images/get?&format=xml&results_per_page=25';

if (location.hostname === 'localhost') {
  url = 'http://localhost:3000/proxy?url=' + url;
}



